  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&family=Staatliches&display=swap'); */
*{
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}
body{
  font-family: 'Lato', sans-serif;
  /* font-family: 'poppins',sans-serif; */
}
.header-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.book-list img{
  width: 100%;
  height: 100%;
  object-fit: cover;

}
.img img{
  width: 100%;
  /* height: 100%; */
  object-fit: cover;
}
.header{
  position: relative;
  
}
.header-img{
  height: 500px;
}

.flex{
  display: flex;
  text-align: center;
  align-items: center;
}
.flex.flex-wrap{
    flex-wrap: wrap;
}
.flex.flex-center{
    align-items: center;
    justify-content: center;
}
.book{
  padding: 60px 0px;
}
.book h1{
  text-decoration: underline;
  margin: 50px;
  text-align: center;
}
.book-list{
  list-style: none;
}
.book-list li{
  width: 21%;
}
.book-list li a{
  text-decoration: none;
  
}
.book-list li img{
  max-height: 500px;
  transition: all ease 0.3s;
}
.book-list li:hover img{
  transform: scale(1.07);
}
.book-list li h3{
  color: black;
  padding: 10px;
}
.br{
  border-radius: 3%;
}
.gap{
  gap: 40px 64px;
}
.container{
  max-width: 1200px;
  margin: auto;
}
.checkout{
  background-color: rgb(44, 41, 48);
  height: 90px;
}
.header .header-info{
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%,-50%);
}
.header .header-info h1{
  font-size: 63px;
  color: aliceblue;
}
.section-list li{
  font-size: 20px;
    padding: 12px;
    color: rgb(105 105 105);
    line-height: 1.5rem;
}
.section{
  background-color: #fafafa;
}
.section h1{
  text-align: center;
  padding-top: 20px;
}
.book .icons{
  display: flex;
  justify-content: space-between;
  font-size: 40px;
  padding: 25px 0px;
}
@media only screen and (max-width: 991px){
.banner_img {
    height: 260px;
}
}
.banner_img {
  height: 200px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #2b2d33;
}
.banner_container {
  max-width: 100%;
  position: relative;
  padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.BannerTitle {
  position: relative;
  z-index: 1;
}
.BannerTitle h1 {
  color: #fff;
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
  font-family: 'Raleway', sans-serif;
}
.BannerTitle h2 {
  color:grey
}
.detail{
  display: flex;
  padding-top: 50px;
  margin: auto;
  width: 100%;
  padding-right: 50px;
  padding-left: 50px;
}

.detail h3{
  padding-top: 4%;
  width: 700px;
  padding-left: 4%;
  color: #343434;
  line-height: 1.5;
}
.img{
  width: 300px;
}

.img button{
  background-color: black;
  color: white;
  border-radius: 3px;
  border: none;
  padding: 8px 18px;
  font-size: 14px;
  margin-top: 20px;
  margin-bottom: 20px;
  cursor: pointer;
}
.img button:hover{
  background-color: #d64b3c;
}

.active button{
  background-color: #F67669;
}
.img .heart{
  height: 30px;
  width: auto;
  opacity: 0; 
  transform: translate(247px,50px);
}
.active .heart{
  opacity: 1;
}
.search-container{
  background-color: #d9d9d9;
  width: 50px;
  height: 50px;
  border-radius: 60px;
  position: relative;
  transition: all ease 0.3s;

}
.search-container .search{
  position: absolute;
  top: 28%;
  left: 14px;
  height: 1em;
  width: 1em;
}
.search-container.active{
  width: 360px;
}
.search-container .input{
  display: none;
}
.search-container.active .input{
  position: relative;
  width: 150px;
  height: 50px;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center; 




}
.search-container.active .input input{
  position: absolute;
  top: 0;
  left: -80px;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background-color: #d9d9d9;


}


.form-container{
  width: 100%;
}

.form-body{
    font-size: 1.6rem;
    font-family: 'poppins',sans-serif;
    font-weight: 400;
    background: #FFFFFF;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    overflow: auto;

}
.form-body::before{
    content: '';
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 46.5rem;
    z-index: -1;
}

form{
    padding: 0 6.4rem;
    background-color: #fafafa;
    max-width: 75rem;
    width: 100%;
    /* min-height: 100vh; */
    margin-top: 3.2rem;
    border-radius:  2rem;
    display: flex;
    flex-direction: column;
    gap: 3.2rem;
    position: relative;
    margin-bottom: 8rem;
    
}
form div:first-child > fieldset legend{
    width: 100%;
    position: absolute;
    left: 0;
    height: 9.3rem;
    background-color: #F67669;
    border-radius:  2rem 2rem 0 0 ;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Staatliches',sans-serif;
    text-transform: uppercase;
    font-size: 2.4rem;
    line-height: 3.4rem;
    color: #FFFFFF;
    border: 0;
    
}form div:first-child  > .input-wrapper:nth-child(2){
    display: flex;
    margin-top: 11rem;
    flex-direction: row;
    gap: 2.2rem;
    
}
.fieldset-wrapper fieldset legend{
    font-family: 'Staatliches',sans-serif;
    font-style: 2.4rem;
    line-height: 3.4rem;
    text-align: center;
    width: 100%;
    border-bottom: 1px solid #E6E6F0;
    text-transform: uppercase;
}
.input-wrapper{
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: 100%;
}
.input-wrapper label{
    color: #4E4958;
}
.input-wrapper input,
.input-wrapper select{
  font-size: 1.6rem;
  width: 100%;
  height: 2.6rem;
  background: #FAFAFC;
  border: 1px solid #E6E6F0;
  border-radius: 8px;
  color: #4E4958;
  line-height: 2.4rem;
  padding: 0px 15px;
}
.input-wrapper textarea{
    width: 100%;
    height: 2.6rem;
    background: #FAFAFC;
    border: 1px solid #E6E6F0;
    border-radius: 8px;
    color: #4E4958;
    line-height: 2.4rem;
}
.input-wrapper input:invalid,
.input-wrapper select:invalid{
    border: 1px solid rgb(207, 103, 103);
}
.input-wrapper select{
    appearance: none;
    -webkit-appearance: none;
    width: 50%;
}
.input-wrapper textarea{
    height: 8.7rem;
    padding: 1rem;
    padding: 1rem;
    color: #293241;
    font-size: 1.6rem;
}
.check-wrapper{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 8px;
}
.check-wrapper input{
    width: 2.4rem;
    height: 2.4rem;
}
.input-wrapper label span{
    font-size: 1.2rem;
    color: #C1BCCC;
    margin-left: 9px;
}
.input-wrapper label span:hover{
    color: #292d33;
}
.fieldset-wrapper{
    display: flex;
    flex-direction: column;
    gap: 3.2rem;
}
.event-day{
    display: flex;
    flex-direction: row;
    gap: 20px;
}
.event-day> div:nth-child(1){
    width: 200%;
}
.fieldset-button{
    width: 100%;
    height: 8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3.2rem;
    gap: 14px;
}
.button{
    max-width: 62.2rem;
    height: 5.6rem;
    width: 100%;
    color: #FFFFFF;
    background: #F67669;
    border-radius: 8px;
    font-family: 'Staatliches',sans-serif;
    font-size: 1.5rem;
    line-height: 2.6rem;
    margin-bottom: 6.4rem;
    cursor: pointer;
    text-transform: uppercase;
}
.button:hover{
    background: #d64b3c;
    font-size: 1.7rem;
    box-shadow: 2px 1px 10px 1px #C1BCCC;
}
.checkout{
  background-color: #2b2d33;
  height: 85px;
}
.wishlist{
  max-width: 1200px;
  margin: auto;
}
.wishlist h1{
  margin: 30px;
    font-size: 40px;
}
.wishlist li{

  border-bottom:1px gainsboro solid ;
  border-top: 1px gainsboro solid;
  display:flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 30px 0px;
  cursor: pointer;
  transition: all ease 0.3s;
  padding: 20px 0px;
}
.wishlist li:hover{
  border-bottom:2px grey solid ;
  border-top: 2px grey solid;
}

.wishlist .wishlist-item{
  display: flex;
  align-items: center;
  width: 570px;
}
.wishlist-item img{
  width: 200px;
}
.wishlist-item h2{
  padding-left: 50px;
}
.wishlist button{
  height: 40px;
  background-color: black;
  color: white;
  border-radius: 3px;
  border: none;
  padding: 8px 35px;
  font-size: 14px;
  cursor: pointer;
}

@media(max-width:600px){
    form div:first-child  > .input-wrapper:nth-child(2){
        flex-direction: column;
    }
    .event-day> div:nth-child(1){
        width: 100%;
    }
    .event-day{
        flex-direction: column;
    }
}
.bg{
  background: #a18380;
}
#event-form p{
  color: #f47669;
  font-size: 22px;
}
@media(max-width:600px){

  .flex {
    display: block;
  }
  .book-list li {
    width: 100%;
    padding: 20px 40px;
  }
  .header .header-info h1 {
    font-size: 40px;
  }
  .detail {
    display: block;
    padding-top: 30px;
    padding-right: 30px;
    padding-left: 30px;
  }
  .img {
    width: 100%;
    
}
.detail h3 {
  width: 100%;
  margin-left: 0%;
}
.BannerTitle h1 {
  padding-top: 30px;
  font-size: 21px;
}
.BannerTitle h2 {
  font-size: 18px;
}
.wishlist li{
  display: block;
  width: 100%;
  padding: 20px;
}
.wishlist-item h2{
  margin-top: 20px;
}
.wishlist-button{
  text-align: center;
    margin-top: 20px;
}
}

@media (min-width: 768px) and (max-width: 1024px){
  .gap {
    gap: 30px;
}
.book-list li {
  width: 22%;
}
.img .heart {
  transform: translate(163px,50px);
}

}

